let _self
export default {
	data() {
		return {
			userInfo:{}
		}
	},
	onLoad:function(){
		_self = this
		_self.getUserInfo()
	},
	methods: {
		userDetail:function(){// 跳转到个人信息详情页面
			uni.navigateTo({
				url:'../content/userDetail'
			})
		},
		getUserInfo:function(){//获取个人信息并填写到页面
			uni.getStorage({
				key:'userInfo',
				success:function(res){
					_self.userInfo = res.data
					console.log(res.data)
				},
				fail:function(){
					console.log('no')
				}
			})
		},
		toDraft:function(){//去我的草稿页面
			uni.navigateTo({
				url:'../content/draft'
			})
		},
		toCheck:function(){
			uni.navigateTo({
				url:'../content/check'
			})
		},
		toSuccess:function(){
			uni.navigateTo({
				url:'../content/success'
			})
		},
		toSet:function(){
			uni.navigateTo({
				url:'../content/set'
			})
		},
		
		
	}
}
